html,
body {
  min-height: 100%;
  position: relative;
  scroll-behavior: smooth;
  scrollbar-gutter: stable;
}

body {
  font-family: "Manrope", serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #1A1A1A;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}
body.is-popup-open {
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  overflow: hidden;
}

.main {
  position: relative;
  flex-grow: 1;
}

.page-heading {
  font-size: 64px;
  line-height: 71px;
  font-weight: 500;
  margin-bottom: 42px;
}
@media only screen and (max-width: 769px) {
  .page-heading {
    font-size: 34px;
    line-height: 41px;
    margin-bottom: 20px;
  }
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 96px;
}
@media only screen and (max-width: 1690px) {
  .container {
    padding: 0 40px;
  }
}
@media only screen and (max-width: 1199px) {
  .container {
    padding: 0 20px;
  }
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

*:focus {
  outline: none !important;
}

canvas {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition: color 0.3s;
}

p {
  margin: 1em 0;
}
p:first-child {
  margin-top: 0;
}
p:last-child {
  margin-bottom: 0;
}

button {
  background-color: transparent;
  padding: 0;
  border: none;
  user-select: none;
  cursor: pointer;
}

img {
  max-width: 100%;
}

.svg-icon {
  fill: currentColor;
}

a,
input,
label,
button {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
}

a,
input,
label,
span,
button::selection {
  background: transparent;
}

a,
input,
label,
button::-moz-selection {
  background: transparent;
}

.slider {
  background-color: red;
  width: 100%;
  height: 300px;
}
.slider div {
  width: 100%;
  height: 200px;
  margin-right: 20px;
  background-color: blue;
}
.slider div:nth-child(1) {
  background-color: green;
}
.slider div:nth-child(2) {
  background-color: yellow;
}

