.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-family: "Manrope", serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 23px;
  letter-spacing: 0.01em;
  text-align: center;
  padding: 20px 42px;
  color: #ffffff;
  background-color: #1E94CE;
  border: none;
  border-radius: 64px;
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
@media (hover: hover) {
  .button:hover {
    background-color: #0971a5;
  }
}
.button--green {
  flex-grow: 1;
  background-color: #33AF73;
  border: 1px solid rgba(26, 26, 26, 0.1607843137);
}
@media (hover: hover) {
  .button--green:hover {
    background-color: #2d9b66;
  }
}
.button--lightgreen {
  flex-grow: 1;
  background-color: rgba(51, 175, 115, 0.2392156863);
  color: #0B766B;
  border: 1px solid rgba(26, 26, 26, 0.1607843137);
}
@media (hover: hover) {
  .button--lightgreen:hover {
    background-color: rgba(39, 136, 89, 0.2392156863);
  }
}