.header {
  position: absolute;
  width: 100%;
  z-index: 10;
}
@media only screen and (max-width: 1272px) {
  .header__button {
    display: none;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  padding: 32px 0;
}
@media only screen and (max-width: 900px) {
  .header__wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    padding: 20px 0;
  }
}
.header__logo {
  display: flex;
  gap: 24px;
  align-items: center;
  margin-right: 32px;
}
.header__logo-caption {
  font-size: 14px;
  line-height: 20px;
  color: #1A1A1A;
  opacity: 0.65;
}
.header__logo-name {
  font-weight: 800;
  font-size: 42px;
  line-height: 51px;
  color: #1A1A1A;
}
@media only screen and (max-width: 900px) {
  .header__logo {
    margin-right: 0;
  }
  .header__logo-caption {
    font-size: 10px;
    line-height: 16px;
  }
  .header__logo-name {
    font-size: 32px;
    line-height: 41px;
  }
}
.header__address {
  display: inline-block;
  font-style: normal;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0.01em;
  text-align: left;
  color: #1A1A1A;
  opacity: 0.72;
}
@media only screen and (max-width: 1272px) {
  .header__address {
    display: none;
  }
}
.header__contacts {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: 24px;
}
.header__contacts a {
  color: #ffffff;
  transition: color 0.3s;
  opacity: 0.96;
  letter-spacing: 0.01em;
}
@media (hover: hover) {
  .header__contacts a:hover {
    color: #e2e2e2;
  }
}
.header__contacts-tel {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
  text-align: center;
}
.header__contacts-mail {
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
}
@media only screen and (max-width: 1272px) {
  .header__contacts {
    margin-right: 0;
  }
}
@media only screen and (max-width: 900px) {
  .header__contacts {
    margin-left: 0;
    display: none;
  }
}
.header__nav {
  display: none;
  width: 100%;
  background-color: rgba(102, 163, 22, 0.831372549);
  padding: 14px 0;
  margin-top: 50px;
  position: relative;
  transition: opacity 0.3s, transform 0.3s, margin 0.3s;
  backdrop-filter: blur(5px);
}
.header__nav-list {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}
.header__nav-item a {
  color: #ffffff;
  transition: opacity 0.3s;
}
.header__nav-item a:hover {
  opacity: 0.7;
}
@media only screen and (max-width: 769px) {
  .header__nav {
    pointer-events: none;
    opacity: 0;
    transform: translateX(-100%);
    position: absolute;
    z-index: 10;
    top: 36px;
    left: 0;
  }
  .header__nav-list {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }
}
.header__burger {
  align-self: flex-start;
  display: none;
  flex-direction: column;
  justify-content: center;
  width: 46px;
  height: 46px;
  border-radius: 6px;
  background-color: #66A316;
  flex-shrink: 0;
  padding: 8px;
  position: relative;
  z-index: 3;
  cursor: pointer;
  transition: background-color 0.3s;
}
.header__burger i {
  display: block;
  height: 3px;
  width: 100%;
  border-radius: 2px;
  background: #fff;
  left: 0;
  top: 0;
  margin-bottom: 6px;
}
.header__burger i:first-child {
  width: 50%;
}
.header__burger i:last-child {
  margin-bottom: 0;
}
@media (hover: hover) {
  .header__burger:hover {
    background-color: #588d13;
  }
}
.header .social {
  display: flex;
  gap: 8px;
  margin-left: 24px;
}
.header .social__icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 64px;
  height: 64px;
  line-height: 0;
  margin-right: 10px;
  background-color: #ffffff;
  border-radius: 50%;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
@media (hover: hover) {
  .header .social__icon:hover {
    background-color: #f3f3f3;
  }
}
@media only screen and (max-width: 900px) {
  .header .social {
    margin-left: 0;
    display: none;
  }
}