.hero {
  margin-bottom: 160px;
}
.hero__container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 0 0 96px;
}
@media only screen and (max-width: 1690px) {
  .hero__container {
    padding: 0 0 0 40px;
  }
}
@media only screen and (max-width: 1199px) {
  .hero__container {
    padding: 0 0 0 20px;
  }
}
@media only screen and (max-width: 900px) {
  .hero__container {
    padding: 0 20px;
  }
}
.hero__wrapper {
  width: 100%;
  gap: 98px;
  display: flex;
}
@media only screen and (max-width: 1690px) {
  .hero__wrapper {
    gap: 28px;
  }
}
@media only screen and (max-width: 900px) {
  .hero__wrapper {
    flex-direction: column-reverse;
  }
}
.hero__content {
  width: 50%;
  padding-top: 303px;
}
@media only screen and (max-width: 1690px) {
  .hero__content {
    padding-top: 203px;
  }
}
@media only screen and (max-width: 1272px) {
  .hero__content {
    padding-top: 153px;
  }
}
@media only screen and (max-width: 900px) {
  .hero__content {
    width: 100%;
    padding-top: 0;
  }
}
.hero__name {
  width: 50%;
  font-size: 64px;
  font-weight: 500;
  line-height: 77px;
  letter-spacing: -0.02em;
  text-align: left;
  margin-bottom: 52px;
}
@media only screen and (max-width: 1272px) {
  .hero__name {
    font-size: 44px;
    line-height: 57px;
  }
}
@media only screen and (max-width: 900px) {
  .hero__name {
    width: 100%;
    font-size: 34px;
    line-height: 47px;
    margin-bottom: 22px;
  }
}
.hero__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.hero__item {
  display: flex;
  align-items: center;
  padding: 16px 0;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.01em;
  text-align: left;
  color: rgba(26, 26, 26, 0.8588235294);
  border-bottom: 1px solid rgba(26, 26, 26, 0.1607843137);
}
@media only screen and (max-width: 900px) {
  .hero__item {
    flex-direction: column;
    text-align: center;
    gap: 8px;
  }
}
.hero__icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 52px;
  height: 52px;
  line-height: 0;
  margin-right: 20px;
  background-color: rgba(26, 26, 26, 0.0392156863);
  border-radius: 50%;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
@media (hover: hover) {
  .hero__icon:hover {
    background-color: #f3f3f3;
  }
}
.hero__controls {
  display: flex;
  gap: 24px;
  justify-content: space-between;
  margin-top: 52px;
  width: 100%;
}
@media only screen and (max-width: 1000px) {
  .hero__controls {
    flex-direction: column;
  }
}
.hero__ph {
  position: relative;
  width: 60%;
  flex-grow: 1;
  border-radius: 24px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  overflow: hidden;
}
.hero__ph::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(13.92deg, rgba(0, 0, 0, 0) 61.09%, rgba(0, 0, 0, 0.36) 88.43%);
}
.hero__ph img {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  object-fit: cover;
  object-position: center;
}
@media only screen and (min-width: 1920px) {
  .hero__ph {
    width: calc(50% + 300px);
    margin-right: -300px;
  }
}
@media only screen and (max-width: 900px) {
  .hero__ph {
    margin-top: 81px;
    height: 100vh;
    width: calc(100% + 40px);
    margin-left: -20px;
    border-radius: 0;
  }
}