.nav {
  width: 100%;
  position: relative;
  z-index: 11;
}
.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}
.nav__item {
  text-transform: uppercase;
  padding: 20px 32px;
  border-radius: 64px;
  flex-shrink: 0;
}
.nav__item.is-current {
  background-color: #F1F1F1;
}
.nav__item a {
  color: rgba(26, 26, 26, 0.8588235294);
}