.footer {
  color: #1A1A1A;
  padding: 42px 0;
  width: 100%;
}
.footer__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__column {
  display: flex;
  flex-direction: column;
}
.footer__logo {
  font-weight: 800;
  font-size: 42px;
  line-height: 51px;
  color: #1A1A1A;
}
.footer__copy {
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0.01em;
  color: rgba(26, 26, 26, 0.7058823529);
}
.footer__studio {
  display: flex;
  gap: 8px;
  align-items: center;
}
.footer__privacy, .footer__studio span {
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0.01em;
  color: rgba(26, 26, 26, 0.7058823529);
}
.footer .social {
  display: flex;
  gap: 8px;
}
.footer .social__icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 64px;
  height: 64px;
  line-height: 0;
  margin-right: 10px;
  background-color: #ffffff;
  border: 2px solid rgba(26, 26, 26, 0.1568627451);
  border-radius: 50%;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
@media (hover: hover) {
  .footer .social__icon:hover {
    background-color: #f3f3f3;
  }
}

@media only screen and (max-width: 769px) {
  .footer {
    padding: 24px 0;
  }
  .footer__wrapper {
    flex-direction: column;
    gap: 16px;
  }
  .footer__column {
    align-items: center;
    gap: 6px;
  }
}